<!-- 메인 페이지 마크업 -->

<template lang="html">
  <div id="main_content" class="main_content">

    <MainVisual />

    <div id="wrap">

      <div class="main_title">
        <h2>Read school</h2>
        <p>지역별 학교를 찾아보세요</p>
      </div>


      <div class="button_box">
        <button v-for="(item, index) in title" :key="index"  type="button" @click="indexBinding(index),classAdd(index)" :class="{active : index === check}">{{item}}</button>
      </div>

      <div class="plus_area"> 
        <label for="search1">상세지역</label>
        <input id="search1" value="" type="text" @keyup="search()" v-model="inputValue" />
        <button @click="search()">검색</button>
      </div>

      <div class="search_num" v-show="message">{{msgtext}}</div>

      <div class="table_box">
        <div v-for="(json,firstindex) in searchAry" :key="firstindex" class="json_table">
            <ul>
              <li>
                <div class="top_table">
                  <span>{{ (firstindex +1) }}</span>
                </div>
                <div class="mid_table">
                  <span>{{ json.schul_nm }}</span>
                  <span><i class="fa-solid fa-map-location"></i> {{ json.org_rdnma }}</span>
                </div>
                <div class="bott_table">
                    <span>{{ json.coedu_sc_nm }}</span>
                    <span><i class="fa-solid fa-phone"></i> {{ json.org_faxno }}</span>
                  </div>
              </li>
            </ul>
          </div>
      </div>

    </div>


  </div>
</template>
<style>

#wrap{
  width: 90%;
  margin: 0 auto;
}
#wrap .main_title{
  margin: 30px 0;
}
#wrap h2{
  text-align: center;
  font-size: 3rem;
  margin-bottom: 10px;

}
#wrap p{
  font-size: 1.25rem;
  text-align: center;

}
.table_box{
  margin-bottom: 100px;
}
.button_box{
  overflow: hidden;

}

.button_box button{
  padding: 10px;
  width: 10%;
  float: left;
  box-sizing: border-box;
  border: 1px solid #ccc;
  transition: all 0.3s ease-out;
}
.button_box button:hover{
  background: #1a3901;
  color:#fff;
  opacity: 0.5;
}

.button_box{
  margin-bottom: 50px;
}
.button_box .active{
  background: #1a3901;
  color: #fff;
}
.plus_area{
  text-align: center;
  margin-bottom: 30px;
}
.plus_area label{
  padding: 10px 20px;
  font-weight: 700;
  font-size: 1.5rem;
}

.plus_area #search1{
  border: 1px solid #333;
  padding: 10px 20px;
  outline: none;
  box-sizing: border-box;
  border-radius: 10px;
}
.plus_area button{
  padding: 10px 20px;
  border: 1px solid #ccc;
  background: #1a3901;
  color: #fff;
  vertical-align: top;
  border-radius: 10px;
  margin-left: 20px;
  opacity: 0.5;
  transition: all 0.3s ease-out;
}
.plus_area button:hover{
  opacity: 1;
}
.search_num{
  font-size: 1.5rem;
  font-weight: 500;
  text-align: center;
  margin-bottom:30px;
}
.json_table{
  font-size: 1.25rem;
  padding: 20px;
  border: 1px solid #ccc;
  font-family:  sans-serif;
  
}
.json_table ul li{
  display: grid;
    grid-template-columns: 1fr 4fr 2fr;
    align-items: center;
}
.json_table .top_table{
  text-align: center;
  font-weight: 700;
}

.json_table .mid_table span{
  overflow: hidden;
  display: block;

}
.json_table .mid_table span:nth-of-type(2n+1){
  margin-bottom: 20px;
  font-weight: 700;
  font-size: 1.5rem;
}
.json_table .mid_table i{
  color: #666;
}
.json_table .bott_table{
  text-align: end;
  position: relative;
}
.json_table .bott_table span:nth-of-type(2n+1){
  position: absolute;
  left: 0px;
}
.json_table .bott_table span:nth-of-type(2n+2){
  position: relative;
  color:#1a3901;
  font-family: 'Fredoka One', sans-serif;
}
.json_table .bott_table span i{
  position: absolute;
  top: 0;
  left: -30px;
  color:#1a3901;
}

@media screen and (max-width: 1024px) {
  .button_box button{
    width: 20%;
  }
  .json_table .bott_table span:nth-of-type(2n+1){
    display: none;
  }
} 
@media screen and (max-width: 640px) {
  .button_box button{
    width: 25%;
  }
  .json_table ul li{
    display: block;
    position: relative;
  }
  .json_table .mid_table span{
    margin-bottom: 30px;
  }
  .json_table .bott_table{
    text-align: center;
  }
  .plus_area label{
    display: block;
  }
  .plus_area #search1{
    display: block;
    margin: 0 auto;
  }
  .plus_area button{
    margin: 0;
    margin-top: 10px;
  }
  .json_table .top_table{
    position: absolute;
    bottom: 0;
    right: 0;
  }
} 
</style>

<script>

 import MainVisual from './Main_visual.vue'

 import gangnamJson from '../assets/1_gangnam.json';
 import gangdongJson from '../assets/2_gangdong.json'
 import gangbugJson from '../assets/3_gangbug.json';
 import gwan_agJson from '../assets/4_gwan-ag.json';
 import gwangjinJson from '../assets/5_gwangjin.json';
 import guloJson from '../assets/6_gulo.json';
 import geumcheonJson from '../assets/7_geumcheon.json';
 import nowonJson from '../assets/8_nowon.json';
 import dobongJson from '../assets/9_dobong.json';
 import dongdaemunJson from '../assets/10_dongdaemun.json';
 import dongjagJson from '../assets/11_dongjag.json';
 import mapoJson from '../assets/12_mapo.json';
 import seodaemunJson from '../assets/13_seodaemun.json';
 import seochoJson from '../assets/14_seocho.json';
 import seongdongJson from '../assets/15_seongdong.json';
 import seongbugJson from '../assets/16_seongbug.json';
 import songpaJson from '../assets/17_songpa.json';
 import yangcheonJson from '../assets/18_yangcheon.json';
 import yeongdeungpoJson from '../assets/19_yeongdeungpo.json';
 import yongsanJson from '../assets/20_yongsan.json';
 import eunpyeongJson from '../assets/21_eunpyeong.json';
 import jongloJson from '../assets/22_jonglo.json';
 import jung_guJson from '../assets/23_jung-gu.json';
 import junglangJson from '../assets/24_junglang.json';
let gangnam = gangnamJson.DATA;
let gangdong = gangdongJson.DATA;
let gangbug = gangbugJson.DATA;
let gwan_ag = gwan_agJson.DATA;
let gwangjin = gwangjinJson.DATA;
let gulo = guloJson.DATA;
let geumcheon = geumcheonJson.DATA;
let nowon = nowonJson.DATA;
let dobong = dobongJson.DATA;
let dongdaemun = dongdaemunJson.DATA;
let dongjag = dongjagJson.DATA;
let mapo = mapoJson.DATA;
let seodaemun = seodaemunJson.DATA;
let seocho = seochoJson.DATA;
let seongdong = seongdongJson.DATA;
let seongbug = seongbugJson.DATA;
let songpa = songpaJson.DATA;
let yangcheon = yangcheonJson.DATA;
let yeongdeungpo = yeongdeungpoJson.DATA;
let yongsan = yongsanJson.DATA;
let eunpyeong = eunpyeongJson.DATA;
let jonglo = jongloJson.DATA;
let jung_gu = jung_guJson.DATA;
let junglang = junglangJson.DATA;

// document.onload = function(){
//   var onclass = document.querySelectorAll('.button_box button');
// }



export default {
  data(){
    return{
      message:false,
      msgtext:'검색 데이터가 없습니다',
      inputValue: '',
      selectAry : gangnam,
      ary: [gangnam,gangdong,gangbug,gwan_ag,gwangjin,gulo,geumcheon,nowon,dobong,dongdaemun,
      dongjag,mapo,seodaemun,seocho,seongdong,seongbug,songpa,yangcheon,yeongdeungpo,
      yongsan,eunpyeong,jonglo,jung_gu,junglang],
      title: [
          '강남구', '강동구', '강북구', '관악구','광진구','구로구', '금천구', '노원구', '도봉구','동대문구','동작구', '마포구', '서대문구', '서초구','성동구', '성북구', '송파구', '양천구'
          ,'영등포구', '용산구', '은평구', '종로구','중구', '중랑구',
        ],
        check : 0,
        firstClass: true,
        searchAry : []
    }
  },
  created:function(){
    this.searchAry = this.selectAry;
  },
  methods:{
    search: function(){
      let text = this.inputValue;
      if(text){
        this.searchAry = this.searchAry.filter(function(element){
          return element.org_rdnma.includes(text);  //조건에 참이면 객체를 배열로 리턴
          //return  element.price >= Number(text);
        });
        
        if(this.searchAry.length==0){
           this.message=true;
           this.msgtext='검색 데이터가 없습니다';
           
        }else{
           this.message=true;
           this.msgtext=this.searchAry.length + ' 개가 검색되었습니다';
        }
      }else{
        this.searchAry = this.selectAry;
      } 
    },
    indexBinding: function(index){
      this.searchAry = this.ary[index];
      this.selectAry = this.ary[index];
      // console.log(this.searchAry , this.ary[index])
    },
    classAdd: function(index){
        this.check = index;
    },



    },
  components:{
      MainVisual,
  }

}
</script>

