import Vue from 'vue'
import Router  from 'vue-router'
//아래에 연결할 서브페이지(컴포넌트)르 import
import main_page from '../components/Main_page.vue';
import sub_page1 from '../components/Sub_page1.vue';
import sub_page2 from '../components/Sub_page2.vue';
import sub_page3 from '../components/Sub_page3.vue';

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    { path: '/vue/', component: main_page},
    { path: '/vue/main', component: main_page},
    { path: '/vue/sub1', component: sub_page1},
    { path: '/vue/sub2', component: sub_page2},
    { path: '/vue/sub3', component: sub_page3},
  ],


})
