
<template>
  <div id="visual">
     <div class="box">
      <swiper class="swiper" :options="swiperOption">
          <swiper-slide class="img_box">
            <img src="../assets/visual1.png" alt="">
            <span class="text_box">child studying with a book</span>
          </swiper-slide>
          <swiper-slide class="img_box">
            <img src="../assets/visual2.png" alt="">
            <span class="text_box">child doing self-study in the library</span>
          </swiper-slide>
          <swiper-slide class="img_box">
            <img src="../assets/visual3.jpg" alt="">
            <span class="text_box">child telling friends</span>
          </swiper-slide>
          <div class="swiper-pagination" slot="pagination"></div>
          <div class="swiper-button-prev" slot="button-prev"></div>
          <div class="swiper-button-next" slot="button-next"></div>
        </swiper>
     </div>
  </div>
</template>

<style>
/* #1a3901 */
.swiper-pagination-bullet{width: 12px; height: 12px;}
 img{width:100%}
     

.swiper {
   height: 100%;
   width: 100%;
}
.img_box{
  position: relative;
}
.text_box{
  position: absolute;
  top: 40%;
  right: 40%;
  transform: translatey(-50%);
  color: green;
  font-size: 7vw;
  font-family: 'Shadows Into Light', cursive;
}
.v-application .text-h2{
  font-family: 'Fredoka One', sans-serif !important;
}
 
.swiper .swiper-slide {
     display: flex;
     justify-content: center;
     align-items: center;
     text-align: center;
     font-weight: bold;
 }
   div .swiper-pagination-bullet-active{
    background: green;
  }
  .swiper-button-prev:after, .swiper-button-next:after{
    color: #fff;
  }



</style>

<script>
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import 'swiper/css/swiper.css'

export default {
  name: 'App',
  components: {
      Swiper,
      SwiperSlide
  },
   data() {
      return {
        swiperOption: {
          slidesPerView: 1,
          loop: true,
          pagination: {
            el: '.swiper-pagination',
            clickable: true
          },
          navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev'
          }
        }
      }
    }
}
</script>